/* Existing CSS */

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-weight: 900;
  background-color: #f5f5f5;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: rgb(56, 140, 184);
  border-color: rgb(56, 140, 184);
  border-radius: 10px;
  size-adjust: auto;
  background-color: rgb(247, 246, 144);
  padding: 10px;
  border: 2px solid rgb(56, 140, 184);
  box-shadow: 2px 1px 3px rgb(56, 140, 184);
}

/* Style for the logo */
.logo {
  width: 70px;
  height: 70px;
  margin-right: 10px; /* Add margin to separate logo from h1 */
}

h1 {
  text-align: center;
  margin: 0;
  font-size: 40px;
  font-weight: 1000;
  color: rgb(56, 140, 184);
  font-family: system-ui;
}

/* Media Queries for Responsiveness */

/* Mobile Devices */
@media (max-width: 768px) {
  .app {
    padding: 10px;
  }
  .header {
    flex-direction: column; /* Stack elements vertically on smaller screens */
    align-items: center;
  }
  .logo {
    margin: 0 0 10px; /* Add margin to separate logo from h1 */
  }
  h1,
  .logo {
    text-align: center; /* Center the text and logo horizontally */
  }
}

/* Tablets and Small Desktops */
@media (min-width: 769px) and (max-width: 1024px) {
  .app {
    padding: 15px;
  }
  .header {
    flex-direction: column; /* Stack elements vertically on medium screens */
    align-items: center;
  }
  .logo {
    margin: 0 0 10px; /* Add margin to separate logo from h1 */
  }
  h1,
  .logo {
    text-align: center; /* Center the text and logo horizontally */
  }
}

/* Large Desktops and Wider Screens */
@media (min-width: 1025px) {
  .app {
    padding: 20px;
  }
  .header {
    justify-content: center; /* Center both elements horizontally */
    align-items: center; /* Center both elements vertically */
  }
  .logo {
    margin-right: 10px; /* Add margin to separate logo from h1 */
  }
  h1,
  .logo {
    text-align: center; /* Center the text and logo horizontally */
  }
}

/* TaskItem.css */

.task-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border: 1px solid rgb(56, 140, 184);
  border-radius: 10px;
  border-color: rgb(56, 140, 184);
  box-shadow: 2px 1px 3px rgb(56, 140, 184); /* Shadow */
  position: relative; /* Ensure positioning context for the notification */
}

.task-status {
  margin-right: 10px;
  padding: 4px 8px;
  border-radius: 10px;
  font-weight: bold;
}

.task-name {
  flex: 1 1;
  padding: 4px 8px;
  border-radius: 10px;
  color: rgb(56, 140, 184);
}
.task-item::before {
  /* Styles for the notification */
  content: "Click to add notes to task"; /* Your notification text */
  position: absolute;
  top: calc(70%); /* Position it just below the task item */
  left: 0;
  border: 1px solid rgba(0, 0, 0, 0.8); /* Add a minimal border to the notification */
  background-color: #ffffff; /* Solid white background for the notification
  color: rgb(0, 0, 0); /* Text color for the notification */
  padding: 3px; /* Add some padding for better readability */
  border-radius: 5px;
  font-size: 10px;
  font-weight: 100;
  white-space: nowrap; /* Prevent text from wrapping */
  opacity: 0; /* Initially hide the notification */
  pointer-events: none; /* Make the notification non-interactive */
  transition: opacity 2s; /* Add a smooth transition for opacity */
}

.task-item:hover::before {
  opacity: 1; /* Show the notification on hover */
}

.task-name.new {
  color: rgb(56, 140, 184);
  font-weight: bold;
  -webkit-animation: newTaskAnimation 0.8s ease-in-out 1;
          animation: newTaskAnimation 0.8s ease-in-out 1; /* Animation for 2 seconds with 2 iterations */
}

@-webkit-keyframes newTaskAnimation {
  0%,
  100% {
    opacity: 0; /* Start and end with opacity 0 (hidden) */
  }
  50% {
    opacity: 1; /* Peak opacity (visible) at 50% of the animation */
  }
}

@keyframes newTaskAnimation {
  0%,
  100% {
    opacity: 0; /* Start and end with opacity 0 (hidden) */
  }
  50% {
    opacity: 1; /* Peak opacity (visible) at 50% of the animation */
  }
}

.task-name.processing {
  color: rgb(56, 140, 184);
  font-weight: 350;

  -webkit-animation: newTaskAnimation 0.8s ease-in-out 1;

          animation: newTaskAnimation 0.8s ease-in-out 1; /* Animation for 2 seconds with 2 iterations */
}

.task-name.done {
  color: rgb(56, 140, 184); /* Use a muted or gray color */
  text-decoration: line-through; /* Add a line-through to the text */
  font-style: normal; /* Reset font style (remove italic if set for processing) */
  font-weight: normal; /* Reset font weight (remove bold if set for new) */
  -webkit-animation: newTaskAnimation 0.8s ease-in-out 1;
          animation: newTaskAnimation 0.8s ease-in-out 1; /* Animation for 2 seconds with 2 iterations */
}

.buttons-right {
  margin-left: auto;
}

button:hover {
  background-color: #ff2d03;
}

