/* TaskList.css */

.task-list {
  margin-block: 20px;
}

/* Optional: You can add additional styles for the tasks' spacing */
.task-item {
  margin-bottom: 10px;
}

.task-form {
  margin-top: 20px;
}

form {
  display: flex;
  border-color: rgb(56, 140, 184);
}

input[type="text"] {
  color: rgb(56, 140, 184);
  flex: 1 1;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid rgb(56, 140, 184);
  background-color: white;
  box-shadow: 2px 1px 3px rgb(56, 140, 184); /* Shadow */
}
input:focus {
  box-shadow: 3px 2px 5px rgb(56, 140, 184); /* Shadow */
  outline: none;
}

button {
  background-color: rgb(135, 249, 135);

  color: rgb(56, 140, 184);
  border: 1px solid rgb(56, 140, 184);
  border-radius: 10px;
  padding: 5px 10px;
  cursor: pointer;
  margin-left: 10px;
  font-weight: 550;
}

button:hover {
  background-color: rgb(135, 249, 135);
  color: rgb(255, 0, 0);
  box-shadow: 2px 1px 3px rgb(56, 140, 184); /* Shadow */
}

/* CSS for the select element with id "filter" */
